html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

a {
    color: inherit;
    text-decoration: none;
}

img {
    max-width: 100%;
}

body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: rgb(231 231 231);
}

/* Сброс стилей */
.list-reset {
   list-style-type: none;
   padding: 0;
   margin: 0; 
}

.container {
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;

}

/* Кнопки */
.btn {
    color: white;
    background-color: rgb(0, 85, 255);
    padding: 15px 30px;
    outline: none;
    border: none;
    border-radius: 0; 
    outline: 1px solid transparent;
    transition: background-color .3s, outline-color .3s;   
}

.btn:hover {
    background-color: rgb(42, 113, 253);
}

.btn:active {
    background-color: rgb(14, 74, 194);

}

.btn:focus {
    outline-color: rgb(0, 85, 255);
}

/* Шапка */
.header {
    background-color: white;
    padding: 10px 0;
    margin-bottom: 15px;

}

/* Навигация */
.navigation {
    display: flex;
    align-items: center;
    gap: 20px;
}

/* Контейнер для страницы */
.page-container {
}
/* Страница */
.page {
    background-color: white;
    padding-top: 20px;
    padding-bottom: 20px;
}

/* Главный заголовок */
.main-title {
    margin-top: 0;
    margin-bottom: 20px;
}

/* Описание */
.desc {
    margin-top: 0;
    margin-bottom: 0;
}

/* Список товаров */
.product-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

/* Карточка товара */
.product-list__item {
    display: flex;
    flex-direction: column;
    border: 2px solid rgb(197, 191, 191);
    padding: 20px;
}

.product-list__title {
    margin-top: 0;
    font-size: 26px;
    margin-bottom: 15px;
}

.product-list__price {
    font-size: 20px;
    margin-bottom: 15px;
}


